import Character from './Character'
import Enemy from './Enemy'
import { DIRS } from '../../utils/constants'
import Boulder from './Boulder'

export default class Player extends Character {
  constructor(id, level) {
    super(id, level)
    this.power = 0.5
    this.moveIntentDir = ''

    const scene = level.scene
    scene.input.keyboard.removeListener('keydown', this.onKeyDown.bind(this))
    scene.input.keyboard.addListener('keydown', this.onKeyDown.bind(this))

    scene.input.keyboard.removeListener('keyup', this.onKeyUp.bind(this))
    scene.input.keyboard.addListener('keyup', this.onKeyUp.bind(this))
  }
  onKeyUp(event) {
    if (this.moveIntentDir === event.key) {
      this.moveIntentDir = ''
      this.pushingDirection = null
    }
  }
  onKeyDown(event) {
    if(!event.repeat && [DIRS.UP, DIRS.DOWN, DIRS.LEFT, DIRS.RIGHT].includes(event.key)) {
      this.moveIntentDir = event.key;
      // calls loop to avoid delay between key press and visual movement at the begining of next loop
      if(!this.isMoving) {
        this.level.playerLoop()
      }
    }
  }
  destroy() {
    const scene = this.level.scene
    // animate die
    scene.input.keyboard.off('keydown')
    scene.input.keyboard.off('keyup')
  }

  die() {
    this.inactive = true
    this.alien.alpha = 0
    this.destroy()
    this.level.onCharacterDie(this)
  }

  overrunBy(el) {
    if (el.constructor.className === Boulder.className) {
      this.level.scene.events.emit('sounds:death_by_rock_hit')
    }
    this.die()
  }
  runningOver(el) {
    if(el.constructor.className === Enemy.className) {
      this.die()
    }
  }
}

Player.className = 'Player'
