import Phaser from 'phaser'
import LevelBase from './levelBase'
import { Player } from '../objects'


class Level5 extends LevelBase {
  constructor() {
    super()
    this.id = 'level_5'
    this.name = 'Hard Work'
  }
  connectToScene(scene) {
    super.connectToScene(scene)
    scene.events.on('update', this.updateLight.bind(this))
  }
  destroy() {
    this.scene.events.off('update', this.updateLight.bind(this))
  }

  updateLight() {
    const layer = this.scene.children.getByName('layer_1')
    if(!layer) {
      return true
    }
    const map = layer.tilemap
    let origin = this.player.position
    this.moveables.forEach(el => {
      var dist = Phaser.Math.Distance.Snake(
          origin.x,
          origin.y,
          el.position.x,
          el.position.y
      );

      el.alien?.setAlpha(1 - 0.1 * dist);
    })
    map.forEachTile((tile) => {
      var dist = Phaser.Math.Distance.Snake(
          origin.x,
          origin.y,
          tile.x,
          tile.y
      );

      tile.setAlpha(1 - 0.1 * dist);
    });

  }

  onInteractWith(el) {
    if (el.order === 0) {
      this.onComplete()
    }
  }
  onAddElement(el) {
    if(el.constructor.name === Player.name) {
      el.power = 1
    }
  }
}

export default Level5
