import LevelBase from './levelBase'
import { Interactive } from '../objects'
import { PIXEL_TO_TILEMAP_POSITION, PIXEL_NAME } from '../../utils/constants'

class Level2 extends LevelBase {
  constructor() {
    super()
    this.id = 'level_2'
    this.name = 'The finding'
  }

  onInteractWith(el) {
    if(el.order === 1) {
      el.alien.destroy()
      const layer = this.scene.children.getByName('layer_1')
      const { tilemap: map } = layer
      this.player.moveIntentDir = ''
      this.scene.events.emit('dialogue:show', {
        participants: ['closeUp_Mary', 'closeUp_Dwarf'],
        conversation: [
          {
            who: 'closeUp_Mary',
            text: [
              "This thing is incredible,",
              "I cut through earth like",
              "butter.",
            ]
          },
          {
            who: 'closeUp_Dwarf',
            text: [
              "The power of the pickaxe",
              "is within you:",
              "Now you can walk through",
              "the ground.",
            ]
          },
          {
            who: 'closeUp_Mary',
            text: [
              "How are you communicating",
              "with me? Telepathy?",
            ]
          },
          {
            who: 'closeUp_Dwarf',
            text: [
               "I've felt new powers",
               "awaken within me since",
               "the comet's arrival.",
               "There are many things",
               "about me you have yet",
              "to understand.",
            ]
          },
        ]
      })
      
      this.player.power = 1
      map.putTileAt(PIXEL_TO_TILEMAP_POSITION[PIXEL_NAME.EMPTY], el.position.x, el.position.y)
    }
    if(el.order === 0) {
      this.onComplete()
    }
  }

  onAddElement(el) {
    if(el.constructor.className === Interactive.className && el.order === 1) {
      const layer = this.scene.children.getByName('layer_1')
      const { tilemap: map } = layer
      const pickaxe = this.scene.add.sprite(48, 16).setOrigin(0, 0.25).play('pickaxe_idle')
      pickaxe.setScale(layer.scaleX)
      pickaxe.x = map.tileToWorldX(el.position.x)
      pickaxe.y = map.tileToWorldY(el.position.y)

      el.alien = pickaxe
    }
  }
}

export default Level2
