import LevelBase from './levelBase'

class Level1 extends LevelBase {
  constructor() {
    super()
    this.id = 'level_1'
    this.name = 'The beginning'
  }

  connectToScene(scene) {
    super.connectToScene(scene)
    scene.events.once('levelstart', () => {
      scene.events.emit('dialogue:show', {
        participants: ['closeUp_Mary', 'closeUp_Dwarf'],
        conversation: [
          {
            who: 'closeUp_Dwarf',
            text: [
              "Well look who decided to",
              "jump into the hole. Let",
              "me guess. You're here to",
              "take all of these precious",
              "mushrooms. But",
              "what about me!",
              "Don't I deserve a few",
              "shrooms?",
              "I'll tell ya what."
            ]
          },
          {
            who: 'closeUp_Dwarf',
            text: [
               "Give me some of your",
               "mushrooms at the end of",
               "this level. Maybe i'll",
               "tell you how to beat the",
               "game. For Now… Why don't",
               "you try moving those",
               "boulders?",
            ]
          },
          {
            who: 'closeUp_Mary',
            text: [
              "I guess I should find",
              "Marduk.",
            ]
          },
          {
            who: 'closeUp_Dwarf',
            text: [
              "You can also reset the",
              "level by pressing escape.",
              "Although i'm not sure",
              "why you'd bother.",
            ]
          },
        ]
      })
    })
  }

  onInteractWith(el) {
    if(el.order === 0) {
      this.onComplete()
    }
  }
}

export default Level1
