import Phaser from 'phaser'
import { SCALE } from '../utils/constants'
import CRTFilter from '../utils/CRTShaderFX'

let titlesMusic
export default class MenuScene extends Phaser.Scene {
  constructor () {
    super('MenuScene');
  }

  preload () {
    const txt = this.add.text(80 * SCALE, (121) * SCALE + 20 * SCALE,
      'LOADING.... PLEASE WAIT',
      {
        fontFamily: 'GameGirl',
        fontSize: "10px",
        fill: "#ffffff",
      }
    )
  
    txt.setScale(SCALE)
    this.load.script('webfont', 'https://ajax.googleapis.com/ajax/libs/webfont/1.6.26/webfont.js');
    this.load.image('main-tiles', process.env.PUBLIC_URL + '/assets/tilemaps/tilemap_20x20.png');
    this.load.image('hud-tiles', process.env.PUBLIC_URL + '/assets/tilemaps/hud_tilemap_20x20.png');
    this.load.image('level_1', process.env.PUBLIC_URL + '/assets/maps/level_1.png');
    this.load.image('level_2', process.env.PUBLIC_URL + '/assets/maps/level_2.png');
    this.load.image('level_3', process.env.PUBLIC_URL + '/assets/maps/level_3.png');
    this.load.image('level_4', process.env.PUBLIC_URL + '/assets/maps/level_4.png');
    this.load.image('level_5', process.env.PUBLIC_URL + '/assets/maps/level_5.png');
    this.load.image('level_6', process.env.PUBLIC_URL + '/assets/maps/level_6.png');
    this.load.image('Prologue', process.env.PUBLIC_URL + '/assets/images/Prologue.png');
    this.load.image('thechild', process.env.PUBLIC_URL + '/assets/images/thechild.png');
    this.load.image('theworld', process.env.PUBLIC_URL + '/assets/images/theworld.png');
    this.load.image('Tiamat', process.env.PUBLIC_URL + '/assets/images/Tiamat.png');
    this.load.image('Bossfight', process.env.PUBLIC_URL + '/assets/images/Bossfight.png');
    this.load.image('Bossfighthands', process.env.PUBLIC_URL + '/assets/images/Bossfighthands.png');
    this.load.spritesheet('boulder', process.env.PUBLIC_URL + '/assets/sprites/boulder_sprite.png', { frameWidth: 26, frameHeight: 26 });
    this.load.spritesheet('brave', process.env.PUBLIC_URL + '/assets/sprites/brave_sprite.png', { frameWidth: 26, frameHeight: 26 });
    this.load.spritesheet('bat', process.env.PUBLIC_URL + '/assets/sprites/bat_sprite.png', { frameWidth: 26, frameHeight: 26 });
    this.load.spritesheet('pickables', process.env.PUBLIC_URL + '/assets/sprites/pickables_sprite.png', { frameWidth: 26, frameHeight: 26 });
    this.load.spritesheet('closeUp', process.env.PUBLIC_URL + '/assets/sprites/close_up_sprite.png', { frameWidth: 26, frameHeight: 26 });
    this.load.audio('credits', process.env.PUBLIC_URL + '/assets/audio/Score/Opening Credits_.mp3');
    this.load.audio('opening_peace', process.env.PUBLIC_URL + '/assets/audio/Score/NormalDay.mp3');
    this.load.audio('opening_quakes', process.env.PUBLIC_URL + '/assets/audio/Score/Earthquakes+Tiamat.mp3');
    this.load.audio('credits', process.env.PUBLIC_URL + '/assets/audio/Score/NormalDay.mp3');
    this.load.audio('levels_surface', process.env.PUBLIC_URL + '/assets/audio/Score/1st 7 Levels.mp3');
    this.load.audio('swarf_chat_music', process.env.PUBLIC_URL + '/assets/audio/Score/MushroomDwarfDialogue(loop).mp3');
    this.load.audio('generic_chat_music', process.env.PUBLIC_URL + '/assets/audio/Score/GenericDialogue(loop).mp3');
    this.load.audio('escape_music', process.env.PUBLIC_URL + '/assets/audio/Score/BossBattle_EscapeTheVoid!(loop).mp3');
    this.load.audio('rock_hit', process.env.PUBLIC_URL + '/assets/audio/SFX/RockHit.mp3');
    this.load.audio('rock_hit2', process.env.PUBLIC_URL + '/assets/audio/SFX/RockHit2.mp3');
    this.load.audio('rock_hit3', process.env.PUBLIC_URL + '/assets/audio/SFX/RockHit3.wav');
    this.load.audio('clear_hit1', process.env.PUBLIC_URL + '/assets/audio/SFX/PickaxeClearingGround1.mp3');
    this.load.audio('clear_hit2', process.env.PUBLIC_URL + '/assets/audio/SFX/PickaxeClearingGround2.mp3');
    this.load.audio('clear_hit3', process.env.PUBLIC_URL + '/assets/audio/SFX/PickaxeClearingGround3.mp3');
    this.load.audio('gem_hit', process.env.PUBLIC_URL + '/assets/audio/SFX/GemHitRock.mp3');
    this.load.audio('shroom_pick', process.env.PUBLIC_URL + '/assets/audio/SFX/MushroomPickUp.mp3');
    this.load.audio('gem_pick', process.env.PUBLIC_URL + '/assets/audio/SFX/DiamondPickUp.mp3');
    this.load.audio('death_by_rock', process.env.PUBLIC_URL + '/assets/audio/SFX/DeathByRock.mp3')
    this.load.audio('level_complete', process.env.PUBLIC_URL + '/assets/audio/SFX/Level Finish.mp3')
    this.load.audio('earthquake', process.env.PUBLIC_URL + '/assets/audio/SFX/Earthquake.mp3')
    // this.load.on(Phaser.Loader.Events.PROGRESS, console.log)
  }

  navigateToScene(sceneId, props = {}) {
    this.cameras.main.fade(500, 0, 0, 0)
    this.tweens.add({  
      targets: titlesMusic,
      volume: 0,
      ease: 'Linear',
      duration: 500,
      onComplete: () => {
        titlesMusic.pause()
        this.scene.start(sceneId, props)
      },
    });
  }

  create() {
    this.cameras.main.setPostPipeline(CRTFilter);
    this.cameras.main.setBackgroundColor("#486859")

    let image = this.add.image(this.cameras.main.width / 2, this.cameras.main.height / 3, 'Bossfight')
    let scaleX = this.cameras.main.width / image.width
    let scaleY = this.cameras.main.height / image.height
    let scale = Math.max(scaleX, scaleY)
    image.setScale(scale * 1.5).setScrollFactor(0)
    image.name = 'background_art'

    window.WebFont.load({
      custom: {
          families: [ 'GameGirl' ]
      },
      active: () => {
        this.cameras.main.fadeIn(500, 0, 0, 0)
        this.events.emit('scene_ready', this.sys.config)

        titlesMusic = this.sound.add('credits', { loop: true });
        titlesMusic.play()
        
        // this.events.once(Phaser.Scenes.Events.WAKE, () => console.log(Phaser.Scenes.Events.WAKE))
        // this.events.once(Phaser.Scenes.Events.UPDATE, () => console.log(Phaser.Scenes.Events.UPDATE))
        // this.events.once(Phaser.Scenes.Events.TRANSITION_WAKE, () => console.log(Phaser.Scenes.Events.TRANSITION_WAKE))
        // this.events.once(Phaser.Scenes.Events.TRANSITION_START, () => console.log(Phaser.Scenes.Events.TRANSITION_START))
        // this.events.once(Phaser.Scenes.Events.TRANSITION_OUT, () => console.log(Phaser.Scenes.Events.TRANSITION_OUT))
        // this.events.once(Phaser.Scenes.Events.TRANSITION_INIT, () => console.log(Phaser.Scenes.Events.TRANSITION_INIT))
        // this.events.once(Phaser.Scenes.Events.TRANSITION_COMPLETE, () => console.log(Phaser.Scenes.Events.TRANSITION_COMPLETE))
        // this.events.once(Phaser.Scenes.Events.START, () => console.log(Phaser.Scenes.Events.START))
        // this.events.once(Phaser.Scenes.Events.SLEEP, () => console.log(Phaser.Scenes.Events.SLEEP))
        // this.events.once(Phaser.Scenes.Events.SHUTDOWN, () => console.log(Phaser.Scenes.Events.SHUTDOWN))
        // this.events.once(Phaser.Scenes.Events.RESUME, () => console.log(Phaser.Scenes.Events.RESUME))
        // this.events.once(Phaser.Scenes.Events.RENDER, () => console.log(Phaser.Scenes.Events.RENDER))
        // this.events.once(Phaser.Scenes.Events.READY, () => console.log(Phaser.Scenes.Events.READY))
        // this.events.once(Phaser.Scenes.Events.PRE_RENDER, () => console.log(Phaser.Scenes.Events.PRE_RENDER))
        // this.events.once(Phaser.Scenes.Events.PAUSE, () => console.log(Phaser.Scenes.Events.PAUSE))
        // this.events.once(Phaser.Scenes.Events.CREATE, () => console.log(Phaser.Scenes.Events.CREATE))
        // this.events.once(Phaser.Scenes.Events.BOOT, () => console.log(Phaser.Scenes.Events.BOOT))

        let currentOption = 0

        const arrow = this.add.graphics({ fillStyle: { color: 0xffffff, alpha: 1 } })
        arrow.fillTriangle(0, 0, 5, 5, 0, 10).setPosition(70 * SCALE, 20 * SCALE)
        // arrow.setPosition(70 * SCALE, ((12 * newOption) + 2) * SCALE)
        arrow.setScale(SCALE)
        
        const menuOptions = [{
          label: 'New Game',
          disabled: false,
          onSelect: () => this.navigateToScene('IntroScene')
        }, {
          label: 'Level 1',
          disabled: false && window.localStorage.getItem('level_1') !== 'true',
          onSelect: () => this.navigateToScene('GameScene', { level: 1 })
        }, {
          label: 'Level 2',
          disabled: false && window.localStorage.getItem('level_2') !== 'true',
          onSelect: () => this.navigateToScene('GameScene', { level: 3 })
        }, {
          label: 'Level 3',
          disabled: false && window.localStorage.getItem('level_3') !== 'true',
          onSelect: () => this.navigateToScene('GameScene', { level: 4 })
        }, {
          label: 'Level 4',
          disabled: false && window.localStorage.getItem('level_4') !== 'true',
          onSelect: () => this.navigateToScene('GameScene', { level: 5 })
        }, {
          label: 'Level 5',
          disabled: false && window.localStorage.getItem('level_5') !== 'true',
          onSelect: () => this.navigateToScene('GameScene', { level: 6 })
        }, {
          label: 'Level 6',
          disabled: false && window.localStorage.getItem('level_6') !== 'true',
          onSelect: () => this.navigateToScene('GameScene', { level: 8 })
        }
        // , {
        //   label: 'Cinematic final',
        //   disabled: false,
        //   onSelect: () => this.navigateToScene('ConnectorScene', { id: 8, name: 'cinematic_last' } )
        // }
        // , {
        //   label: 'Cinematic final',
        //   disabled: false,
        //   onSelect: () => this.navigateToScene('ConnectorScene', { id: 7, name: 'cinematic_escape' } )
        // }
      ]

        menuOptions.forEach((o, idx) => {
          const txt = this.add.text(80 * SCALE, (12 * idx + 1) * SCALE + 20 * SCALE,
            o.label,
            {
              fontFamily: 'GameGirl',
              fontSize: "10px",
              fill: "#ffffff",
            }
          ).setVisible(!o.disabled)
          txt.displayHeight = 10 * SCALE
          txt.setScale(SCALE)
          // txt.displayWidth = idx * 10 * SCALE

        })

        this.input.keyboard.on('keydown', event => {
          let newOption = false
          if (event.key === 'ArrowUp') {
            newOption = currentOption > 0 ? currentOption - 1 : menuOptions.length - 1
          }
          if (event.key === 'ArrowDown') {
            newOption = currentOption < menuOptions.length - 1 ? currentOption + 1 : 0
          }
          if(newOption !== false && !menuOptions[newOption].disabled) {
            arrow.setPosition(70 * SCALE, ((12 * newOption) + 2) * SCALE + 20 * SCALE)
            currentOption = newOption
          }
          if (event.key === 'Enter') {
            if(!menuOptions[currentOption].disabled) {
              menuOptions[currentOption].onSelect()
            }
          }
        })
      }
    })
  }
}
