import GameObject from './GameObject'

export default class Destroyable extends GameObject {
  constructor(props) {
    super(props)
    this.inactive = false
  }
  overrunBy(el) {
    if(el.power > this.strength) {
      this.level.onDestroyElement(this)
      this.inactive = true
    }
  }
}

Destroyable.className = 'Destroyable'
