import LevelBase from './levelBase'
import { Player } from '../objects'

class Level4 extends LevelBase {
  constructor() {
    super()
    this.id = 'level_4'
    this.name = 'The encounter'
  }

  onInteractWith(el) {
    if(el.order === 0) {
      this.onComplete()
    }
  }
  onAddElement(el) {
    if(el.constructor.name === Player.name) {
      el.power = 1
    }
  }
}

export default Level4
