import Phaser from 'phaser'
import { SCALE, TILES_BY_SCREEN, TILE_SIZE } from '../utils/constants'
import CRTFilter from '../utils/CRTShaderFX'
import UI from '../components/UI'

export default class IntroScene extends Phaser.Scene {
  constructor () {
    super('IntroScene');
  }
  
  preload () {
    window.localStorage.setItem('diamonds', 0)
    window.localStorage.setItem('shrooms', 0)
    window.localStorage.setItem('level_1', 'false')
    window.localStorage.setItem('level_2', 'false')
    window.localStorage.setItem('level_3', 'false')
    window.localStorage.setItem('level_4', 'false')
    window.localStorage.setItem('level_5', 'false')
    window.localStorage.setItem('level_6', 'false')
  }

  createAnimations() {
    this.anims.create({
      key: 'closeUp_Mary',
      frames:this.anims.generateFrameNumbers('closeUp', { frames: [ 0 ] }),
      repeat: 0,
    })
    this.anims.create({
      key: 'closeUp_Marduk',
      frames:this.anims.generateFrameNumbers('closeUp', { frames: [ 1 ] }),
      repeat: 0,
    })
    this.anims.create({
      key: 'closeUp_Dwarf',
      frames:this.anims.generateFrameNumbers('closeUp', { frames: [ 2 ] }),
      repeat: 0,
    })
    this.anims.create({
      key: 'closeUp_Tiamat',
      frames:this.anims.generateFrameNumbers('closeUp', { frames: [ 3 ] }),
      repeat: 0,
    })
    this.anims.create({
      key: 'closeUp_Pickaxe',
      frames:this.anims.generateFrameNumbers('closeUp', { frames: [ 4 ] }),
      repeat: 0,
    })
    this.anims.create({
      key: 'closeUp_Mushroom',
      frames:this.anims.generateFrameNumbers('closeUp', { frames: [ 5 ] }),
      repeat: 0,
    })
  }
  create() {
    this.createAnimations()
    this.normalDayMusic = this.sound.add('opening_peace', { loop: true })
    this.normalDayMusic.play();
    this.quakesMusic = this.sound.add('opening_quakes', { loop: true });

    const ui = new UI(this, true)

    this.cameras.main.setPostPipeline(CRTFilter);
    let image = this.add.image(this.cameras.main.width / 2, this.cameras.main.height / 2, 'Prologue')
    let scaleX = this.cameras.main.width / image.width
    let scaleY = this.cameras.main.height / image.height
    let scale = Math.max(scaleX, scaleY)
    image.setScale(scale * 1.5).setScrollFactor(0)
    image.name = 'background_art'

    this.tweens.add({
      targets: image,
      scale: scale,
      duration: 10000,
      ease: 'Linear'
    })
    const line1 = this.add.text(80, 10, "",
      {
        fontFamily: 'GameGirl',
        fontSize: "10px",
        fill: "#ffffff",
      }
    ).setScale(scaleX * 0.9).setPosition(2 * scaleX, this.cameras.main.height - 10 * 4 * scaleY)
    line1.displayHeight = 10 * scaleY
    const line2 = this.add.text(80, 10, "",
      {
        fontFamily: 'GameGirl',
        fontSize: "10px",
        fill: "#ffffff",
      }
    ).setScale(scaleX * 0.9).setPosition(2 * scaleX, this.cameras.main.height - 10 * 3 * scaleY)
    line2.displayHeight = 10 * scaleY
    const line3 = this.add.text(80, 10, "",
      {
        fontFamily: 'GameGirl',
        fontSize: "10px",
        fill: "#ffffff",
      }
    ).setScale(scaleX * 0.9).setPosition(2 * scaleX, this.cameras.main.height - 10 * 2 * scaleY)
    line3.displayHeight = 10 * scaleY

    line1.alpha = line2.alpha = line3.alpha = 0
    
    line1.setText('In the beginning: There was nothing,')
    line2.setText('except for the goddess tiamat')
    
    this.tweens.add({
      targets: [line1, line2, line3],
      alpha: 1,
      duration: 1000
    })

    setTimeout(() => {
      this.tweens.add({
        targets: [line1, line2, line3],
        alpha: 0,
        duration: 1000,
        onComplete: () =>  {
          line1.setText('she formed the world as she pleased')
          line2.setText('Giving life to the sumarian people,')
          line3.setText('creating them in her image')

          this.tweens.add({
            targets: [line1, line2, line3],
            alpha: 1,
            duration: 1000
          })
        }
      })
      
    }, 8000);

    setTimeout(() => {
      this.tweens.add({
        targets: [line1, line2, line3],
        alpha: 0,
        duration: 1000,
        onComplete: () =>  {
          line1.setText('The ancient land of Sumaria. An')
          line2.setText('eternal paradise where immortal')
          line3.setText('beings live in infinite harmony')

          this.tweens.add({
            targets: [line1, line2, line3],
            alpha: 1,
            duration: 1000,
            onComplete: () => {
              setTimeout(() => {
                this.tweens.add({
                  targets: [line1, line2, line3],
                  alpha: 0,
                  duration: 1000,
                  onComplete: () => {
                    this.showFirstDialogs()
                  }
                })
              }, 8000);
            }
          })
        }
      })
      
    }, 8000 * 2);
    // this.showFirstDialogs()
    // this.showSecondDialogs()
  }

  showFirstDialogs() {
    // hack to get control after dialogue close
    this.levels = {
      current: {
        pause: () => false,
        resume: () => {
          setTimeout(() => {
            this.showSecondDialogs()
          }, 1000);
        }
      }
    }
    this.events.emit('dialogue:show', {
      participants: ['closeUp_Mary', 'closeUp_Tiamat', 'closeUp_Marduk'],
      conversation: [
        {
          who: 'closeUp_Tiamat',
          text: [
            "My beloved: why do you",
            "call for me?",
          ]
        },
        {
          who: 'closeUp_Mary',
          text: [
            "Our lord. A mysterious",
            "comet dashes across the",
            "sky. Our land has been",
            "ravaged by monstrous",
            "quakes, mysterious holes",
            "have been appearing,",
            "those who fall in never",
            "return. Please my lady,",
            "calm the earth. Let us",
            "rest in peace.",
          ]
        },
        {
          who: 'closeUp_Tiamat',
          text: [
            "The Catalyst Comet.",
            "C/1995 O1 It appears once",
            "every 2,533 years. I do",
            "not control its path.",
            "With it’s coming.",
            "Calamity Strikes."
          ]
        },{
          who: 'closeUp_Mary',
          text: [
            "We, your children, beseech",
            "thee. Guide us towards a",
            "resolution that might save",
            "our people."
          ]
        },
        {
          who: 'closeUp_Tiamat',
          text: [
            "The Guardian ventures",
            "within the holes created",
            "in the earth. You must",
            "discover the purpose of",
            "these mushrooms that now",
            "proliferate the land.",
            "Collect them and deliver",
            "them to me at the core of",
            "the planet."
          ]
        },
        {
          who: 'closeUp_Tiamat',
          text: [
            "Perhaps within these",
            "mysterious objects lies a",
            "solution to the terrors of",
            "The Catalyst Comet."
          ]
        }
      ]
    })
    
  }

  showSecondDialogs() {
    // hack to get control after dialogue close
    this.levels = {
      current: {
        pause: () => false,
        resume: () => {
          this.tweens.add({
            targets: this.children.getByName('background_art'),
            alpha: 0,
            duration: 1000
          })
          this.tweens.add({
            targets: this.normalDayMusic,
            volume: 0,
            duration: 500,
            onComplete: () => {
              this.normalDayMusic.stop()
              this.quakesMusic.play()
              setTimeout(() => {
                this.showThirdDialogs()
              }, 1000);
            }
          })
        }
      }
    }
    this.events.emit('dialogue:show', {
      participants: ['closeUp_Mary', 'closeUp_Marduk'],
      conversation: [
        {
          who: 'closeUp_Marduk',
          text: [
            "-Sigh-",
            "It is a man's duty",
            "I suppose.",
          ]
        },
        {
          who: 'closeUp_Mary',
          text: [
            "Then I shall",
            "accompany you:",
            "There is safety",
            "in numbers.",
          ]
        },
        {
          who: 'closeUp_Marduk',
          text: [
            "Nonsense. How would a",
            "frail priestess be of",
            "any benefit? ",
          ]
        },
        {
          who: 'closeUp_Mary',
          text: [
            "I am not frail. I am",
            "actually well versed in",
            "digging. The maintenance",
            "of the church keeps my",
            "mind and body active,",
            "I am frequently required",
            "to lift heavy objects and",
            "despite my size have",
            "no problems doing so.",
          ]
        },
        {
          who: 'closeUp_Marduk',
          text: [
            "I am a man, to endanger a",
            "woman's life by accepting",
            "her aid in such a perilous",
            "task would disgrace me.",
          ]
        },
        {
          who: 'closeUp_Mary',
          text: [
            "Why must you always be",
            "like this Marduk? I",
            "remember when we used to",
            "play in the garden as",
            "children. You haven’t",
            "always been this stubborn",
            "and aggressive. I know the",
            "current state of the world",
            "has us all in a panic."
          ]
        },
        {
          who: 'closeUp_Mary',
          text: [
            "But come to your senses.",
            "Let me help you. There",
            "are greater things at",
            "stake than your pride.",
          ]
        },
        {
          who: 'closeUp_Marduk',
          text: [
            "I don't need your help.",
          ]
        },
      ]
    })
  }

  showThirdDialogs() {
    // hack to get control after dialogue close
    this.levels = {
      current: {
        pause: () => false,
        resume: () => {
          this.tweens.add({
            targets: this.quakesMusic,
            volume: 0,
            duration: 500,
          })
          setTimeout(() => {
            this.scene.start('GameScene', { level: 1 })
          }, 1000);
        }
      }
    }

    this.events.emit('dialogue:show', {
      participants: [],
      conversation: [
        {
          who: '',
          text: [
            "- A fissure opens up",
            "beneath Marduk and he",
            "falls into",
          ]
        },
        {
          who: '',
          text: [
            "- Mary rolls her eyes",
            "and jumps in after",
          ]
        },
      ]
    })
  }
  animateIntro() {

    this.cameras.main.pan(this.renderer.width, this.renderer.height/2, 2000, 'Sine.easeInOut');
    this.cameras.main.once(Phaser.Cameras.Scene2D.Events.PAN_COMPLETE, () => {
      this.cameras.main.shake(2000, 0.02)
      this.tweens.add({
        targets: this.children.getByName('dark'),
        y: -this.renderer.height * 0.3,
        duration: 2000
      })
      this.tweens.add({
        targets: this.children.getByName('partner'),
        y: this.renderer.height,
        delay: 2000,
        duration: 2000,
        ease: 'Sine.in'
      })
      this.tweens.add({
        targets: this.children.getByName('player'),
        y: this.renderer.height,
        x: {
          value: this.children.getByName('player').x + 150,
          duration: 1000,
          ease: 'Cubic.easeOut',
        },
        delay: 4000,
        duration: 2000,
        ease: 'Back.easeInOut',
        onComplete: () => {
          this.cameras.main.fade(1000, 0, 0, 0)
          this.cameras.main.once('camerafadeoutcomplete', () => void this.scene.start('GameScene', { level: 1}))
        }
      })

    })
  }
}
